@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap');


#twitter, #btn__zuri, #books, #books__python, #pitch, #book__design, #contact {
    color: #101828;
    display: block;
    width: 50%;
    height: 30px;
    background: #eaecf0;
    padding: 10px;
    text-align: center;
    margin-left: auto;
    text-decoration: none;
    margin-right: auto;
    border-radius: 5px;
    font-weight: bold;
    line-height: 28px;
    font-style: normal;
    font-family: 'Inter', sans-serif;
    font-size: 14px;
}

#twitter {
    margin-top: 25px !important;
}

.photos {
    display: flex;
    justify-content:center
}
.pic {
    height: 25px;
    width: 25px;
    margin-right: 7px;
}

