@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');
* {
  padding: 0;
  margin: 0;
  outline: 0;
  border: none;
}

.contact{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh; 
  font-family: 'Inter', sans-serif;
}

.contactContent{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column; 
  width: 600px;
}

.heading{
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.heading > h1, .heading > h5{
  display: inline-block;
}

.heading > h5{
  font-size: 15px;
  text-align: left;
  margin-top: 5px;
  color: #475467;
}

.form{
  margin-top: 24px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px 30px;
  width: 100%;
}

.field{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  flex-grow: 1;
}

.small_field{
  width: 100px !important;
}

.field > input, .field > textarea{
  width: 100%;
  padding: 9px 15px;
  border: 1px solid #D0D5DD;
  font-weight: 100;
  font-size: 16px;
  border-radius:  2px;
  resize: none;
}

.field > textarea{
  height: 100px;
}

textarea {
    margin-top: 3px !important;
}

input[type="text"] {
    margin-top: 3px;
}

.basictxt {
    margin-left: 5px;
    font-size: 14px;
}

#btn__submit{
  width: 100%;
  padding: 10px;
  border-radius: 3px;
  margin-top: 10px;
  background-color: #1570ef;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
}

























